import React, { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import DatePicker from "react-datepicker";
import Api from "../../helpers/Api";
import moment from "moment";

import "react-datepicker/dist/react-datepicker.css";
import ContentHeader from "../../components/ContentHeader";

function ManageCustomer(props) {
  //eventTitle, date, deadline, location, description
  //const { id = 0 } = useParams();
  const { id = 0 } = useParams();
  const [eventTitle, setEventTitle] = useState("");
  const [date, setDate] = useState(moment("2024-05-30 00:00:00").toDate());
  const [deadline, setDeadline] = useState(
    moment("2024-05-30 00:00:00").toDate()
  );
  const [location, setLocation] = useState("");
  const [description, setDescription] = useState("");

  const navigate = useNavigate();

  //load for the edit case
  useEffect(() => {
    if (id) {
      //console.log("Pre Current ID: ", id);
      Api.getEvent(id)
        .then((res) => res.json())
        .then((event) => {
          console.log("Current ID: ", id);
          console.log("Event data received:", event);
          const { eventTitle, date, deadline, location, description } = event;
          // event.date = date.substring(0, date.length - 5);
          // event.deadline = deadline.substring(0, deadline.length - 5);
          // Ensure date and deadline are valid before setting state
          const validDate = moment(date, moment.ISO_8601, true).isValid()
            ? moment(date).toDate()
            : moment().toDate();
          const validDeadline = moment(
            deadline,
            moment.ISO_8601,
            true
          ).isValid()
            ? moment(deadline).toDate()
            : moment().toDate();

          setDate(validDate);
          setDeadline(validDeadline);
          setEventTitle(eventTitle);
          // setDate(date);
          // setDeadline(deadline);
          setLocation(location);
          setDescription(description);
        });
    }
  }, [id]);

  const validateData = () => {
    const now = moment().startOf("day"); // Get the start of today
    const eventDate = moment(date).startOf("day");
    const regDeadline = moment(deadline).startOf("day");

    return (
      eventTitle.trim().length > 0 &&
      location.trim().length > 0 &&
      description.trim().length > 0 &&
      eventDate.isSameOrAfter(now) && // Checks if the event date is today or in the future
      regDeadline.isSameOrAfter(now) && // Checks if the deadline is today or in the future
      regDeadline.isSameOrBefore(eventDate) // Checks if the deadline is the same as or before the event date
    );
  };

  const handleSubmit = (e) => {
    //prevent the normal form submit
    e.preventDefault();

    if (id === 0) {
      //create case
      if (validateData()) {
        console.log("oh jolly me");
        Api.createEvent({
          eventTitle,
          date,
          deadline,
          location,
          description,
        }).then((data) => {
          navigate("/searchCustomers");
        });
      } else {
        const now = moment().startOf("day"); // Get the start of today
        const eventDate = moment(date).startOf("day");
        const regDeadline = moment(deadline).startOf("day");
        if (!eventDate.isSameOrAfter(now)) {
          window.alert("Event date must be today or in the future");
        } else if (!regDeadline.isSameOrAfter(now)) {
          window.alert("Deadline must be today or in the future");
        } else if (!regDeadline.isSameOrBefore(eventDate)) {
          window.alert("Deadline must be the same as or before the event date");
        }
      }
    } else {
      //edit case
      if (validateData()) {
        Api.updateEvent(id, {
          eventTitle,
          date,
          deadline,
          location,
          description,
        }).then((data) => {
          navigate("/searchCustomers");
        });
      }
    }
  };

  const headerLabel = id === 0 ? "Create New Event" : "Edit Event";

  return (
    <>
      <ContentHeader
        title={id === 0 ? "Create New Event" : "Edit Event"}
        links={[
          ...(id !== 0
            ? [
                {
                  to: "/",
                  label: "Home",
                },
              ]
            : []),
          {
            to: id === 0 ? "/" : "/searchCustomers",
            label: id === 0 ? "Home" : "My Created Events",
          },
          {
            label: headerLabel,
            isActive: true,
          },
        ]}
      />

      <section className="content" key="content">
        <div className="card card-primary">
          <div className="card-header">
            <h2 className="card-title">{headerLabel}</h2>
          </div>
          <form onSubmit={handleSubmit}>
            <div className="card-body">
              <div className="form-group">
                {" "}
                <label htmlFor="inputEventTitle">Event Title</label>
                <input
                  type="text"
                  id="inputEventTitle"
                  required
                  className="form-control"
                  value={eventTitle}
                  onChange={(e) => setEventTitle(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputLocation">Location</label>
                <input
                  type="text"
                  id="inputLocation"
                  required
                  className="form-control"
                  value={location}
                  onChange={(e) => setLocation(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputDescription">Description</label>
                <input
                  type="text"
                  id="inputDescription"
                  required
                  className="form-control"
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                />
              </div>
              <div className="form-group">
                <label htmlFor="inputName">Date of Event (dd/mm/yyyy)</label>
                <div className="input-group">
                  <DatePicker
                    dateFormat="dd/MM/yyyy"
                    selected={date}
                    onChange={(date) => {
                      console.log("#date: ", date);
                      setDate(date);
                    }}
                    customInput={<input className="form-control" />}
                  />
                </div>
              </div>
              <div className="form-group">
                <label htmlFor="inputName">
                  Deadline For Registration (dd/mm/yyyy)
                </label>
                <div className="input-group">
                  <DatePicker
                    dateFormat="dd/MM/yyyy"
                    selected={deadline}
                    onChange={(deadline) => {
                      console.log("#deadline: ", deadline);
                      setDeadline(deadline);
                    }}
                    customInput={<input className="form-control" />}
                  />
                </div>
              </div>
            </div>
            <div className="card-footer">
              <Link to="/searchCustomers">
                <button className="btn btn-default" type="button">
                  Cancel
                </button>
              </Link>
              <button className="btn btn-primary float-right" type="submit">
                Submit
              </button>
            </div>
          </form>
        </div>
      </section>
    </>
  );
}

export default ManageCustomer;
