import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Container, Row, Col, Card, Button, Form } from "react-bootstrap";
import Modal from "react-responsive-modal";
import moment from "moment";
import DataTable from "react-data-table-component";
import DatePicker from "react-datepicker";

import Api from "../../helpers/Api";
import "react-responsive-modal/styles.css"; //Import css
import ContentHeader from "../../components/ContentHeader";

function RegisterForEvent(props) {
  //console.log(localStorage.getItem('token'));

  const [data, setData] = useState([]);
  const [filterText, setFilterText] = useState("");
  const [filterType, setFilterType] = useState("eventTitle");
  const [selectedDate, setSelectedDate] = useState(null);

  const navigate = useNavigate();

  useEffect(() => {
    reloadData();
  }, []);

  const reloadData = () => {
    Api.getAllEvents()
      .then((res) => res.json())
      .then((events) => {
        console.log("Events fetched successfully");
        for (const event of events) {
          const { eventTitle, date, deadline, location, description } = event;
          event.date = date.substring(0, date.length - 5);
          event.deadline = deadline.substring(0, deadline.length - 5);
        }
        setData(events);
      });
  };
  const handleRegisterEvent = async (eventId) => {
    let isRegistered = false;
    let isEventAvailForRegistration = false;

    await Api.checkUserAlreadyRegisteredForEvent(eventId)
      .then((response) => response.json()) // Assuming the server responds with JSON
      .then((data) => {
        console.log("Registration status:", data);
        // Assuming 'data' contains the boolean value directly or an object from which the boolean can be extracted
        // For example, if the server returns { registered: true/false }
        isRegistered = data.isRegistered;
        console.log("Is user already registered for the event?:", isRegistered);
      })
      .catch((error) => {
        console.error("Error checking registration status:", error);
      });

    await Api.checkEventRegistrationAbility(eventId)
      .then((response) => response.json()) // Assuming the server responds with JSON
      .then((data) => {
        console.log("Registration ability:", data);
        // Assuming 'data' contains the boolean value directly or an object from which the boolean can be extracted
        // For example, if the server returns { registered: true/false }
        isEventAvailForRegistration = data.isEventAvailForRegistration;
        console.log(
          "Can I still register for the event?:",
          isEventAvailForRegistration
        );
      })
      .catch((error) => {
        console.error("Error checking EventAvailForRegistration:", error);
      });

    if (isEventAvailForRegistration == false) {
      window.alert("Deadline for event registration has already passed.");
    } else if (isRegistered) {
      window.alert("You have already registered for this event.");
    } else if (!isRegistered && isEventAvailForRegistration) {
      console.log("Attempting to register for event:", eventId);
      if (window.confirm(`Do you want to register for event?`)) {
        Api.registerForEvent(eventId).then(() => {
          reloadData();
          navigate("/searchRegistrations");
        });
      }
    } else {
      window.alert("Something strange is afoot");
    }
  };

  const handleFilterTextChange = (e) => {
    setFilterText(e.target.value);
  };

  const handleFilterTypeChange = (e) => {
    setFilterType(e.target.value);
    setFilterText("");
    setSelectedDate(null);
  };

  const getFilteredEvents = () => {
    return data.filter((event) => {
      if (filterType === "date" && selectedDate) {
        return moment(event.date).isSame(selectedDate, "day");
      }
      return event[filterType]
        ?.toLowerCase()
        .includes(filterText.toLowerCase());
    });
  };

  // Function to render individual cards
  const renderEventCard = (event) => (
    <Col md={4} key={event.eventId} className="d-flex align-items-stretch">
      <Card
        className="shadow-lg p-3 mb-5 bg-body rounded"
        border="info"
        style={{ width: "18rem" }}
      >
        <Card.Header as="h5" className="bg-primary text-white">
          {event.eventTitle}
        </Card.Header>
        {event.image && (
          <Card.Img variant="top" src={event.image} alt={event.eventTitle} />
        )}
        <Card.Body className="d-flex flex-column bg-light">
          <Card.Text className="flex-grow-1">{event.description}</Card.Text>
          <Card.Text>
            <strong>Location:</strong> {event.location}
          </Card.Text>
          <Card.Text>
            <strong>Date:</strong> {moment(event.date).format("DD/MM/YYYY")}
          </Card.Text>
          <Card.Text>
            <strong>Deadline:</strong>{" "}
            {moment(event.deadline).format("DD/MM/YYYY")}
          </Card.Text>
          <Button
            variant="success"
            onClick={() => handleRegisterEvent(event.eventId)}
            className="mt-auto"
          >
            Register
          </Button>
        </Card.Body>
        {/* <Card.Footer>
          <small className="text-muted">Only on Event Echo</small>
        </Card.Footer> */}
      </Card>
    </Col>
  );

  return (
    <>
      <ContentHeader
        title="Register For Events"
        links={[
          { to: "/", label: "Home" },
          { label: "Register For Events", isActive: true },
        ]}
      />
      <Container fluid>
        <Row className="mb-3">
          <Col md={3}>
            {" "}
            {/* Adjust this to align with the start of the DataTable */}
            <Form.Control
              as="select"
              value={filterType}
              onChange={handleFilterTypeChange}
            >
              <option value="eventTitle">Event Title</option>
              <option value="description">Description</option>
              <option value="location">Location</option>
              <option value="date">Date</option>
            </Form.Control>
          </Col>
          <Col md={3}>
            {" "}
            {/* Adjust this as well if needed */}
            {filterType === "date" ? (
              <DatePicker
                selected={selectedDate}
                onChange={(date) => setSelectedDate(date)}
                className="form-control"
                dateFormat="dd/MM/yyyy"
                placeholderText="Select a date"
              />
            ) : (
              <Form.Control
                type="text"
                value={filterText}
                onChange={handleFilterTextChange}
                placeholder={`Search by ${filterType}`}
              />
            )}
          </Col>
        </Row>
      </Container>
      <section className="content" key="content" >
        <div className="row">
          <div className="col-12">
            <div className="card" >
              {/* ... card header */}
              <div className="card-body">

                  <Row>
                    {getFilteredEvents().map((event) => renderEventCard(event))}
                  </Row>

              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default RegisterForEvent;

