import React from "react";
import logo from "../../assets/logo.png";
import { Link } from "react-router-dom";
import { useAuth } from "../../context/AuthContext"; // Adjust the path accordingly

function Sidebar() {
  //const [isAuth, setIsAuth] = useState(isAuthenticated());
  const { isAuthenticated, logout } = useAuth();

  const handleLogout = () => {
    logout(); // This updates isAuthenticated state
  };



  return isAuthenticated ? (
    <aside className="main-sidebar sidebar-dark-primary elevation-4">
      <Link to="/" className="brand-link">
        <a href="/" className="brand-link">
          <img
            src={logo}
            alt="AdminLTE Logo"
            className="brand-image img-circle elevation-3"
            style={{ opacity: ".8" }}
          />
          <span className="brand-text font-weight-light">
            <b>Event</b>Echo
          </span>
        </a>
      </Link>
      <div className="sidebar">
        <nav className="mt-2">
          <ul
            className="nav nav-pills nav-sidebar flex-column"
            data-widget="treeview"
            role="menu"
            data-accordion="false"
          >
            <li className="nav-item">
              <Link to="/manageCustomer" className="nav-link">
                <a href="/" className="nav-link">
                  <i className="nav-icon fa fa-plus-circle" style={{ fontSize: '1.4em' }}/>
                  <p>Create New Event</p>
                </a>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/searchCustomers" className="nav-link">
                <a href="/" className="nav-link">
                  <i className="nav-icon fas fa-tasks" style={{ fontSize: '1.4em' }}/>
                  <p>My Created Events</p>
                </a>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/registerForEvent" className="nav-link">
                <a href="/" className="nav-link">
                  <i className="nav-icon fas fa-ticket-alt" style={{ fontSize: '1.4em' }}/>
                  <p>Register For Events</p>
                </a>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/searchRegistrations" className="nav-link">
                <a href="/" className="nav-link">
                  <i className="nav-icon fas fa-calendar-check" style={{ fontSize: '1.4em' }}/>
                  <p>View my Registrations</p>
                </a>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/profilePage" className="nav-link">
                <a href="/" className="nav-link">
                  <i className="nav-icon fa fa-user" style={{ fontSize: '1.4em' }} />
                  <p>Profile Page</p>
                </a>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/login" className="nav-link" onClick={handleLogout}>
                <a href="/" className="nav-link">
                  <i className="nav-icon fa fa-sign-out-alt" style={{ fontSize: '1.4em' }}  />
                  <p>Logout</p>
                </a>
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </aside>
  ) : (
    <aside className="main-sidebar sidebar-dark-primary elevation-4">
      <Link to="/" className="brand-link">
        <a href="/" className="brand-link">
          <img
            src={logo}
            alt="AdminLTE Logo"
            className="brand-image img-circle elevation-3"
            style={{ opacity: ".8" }}
          />
          <span className="brand-text font-weight-light">
            <b>Event</b>Echo
          </span>
        </a>
      </Link>
      <div className="sidebar">
        <nav className="mt-2">
          <ul
            className="nav nav-pills nav-sidebar flex-column"
            data-widget="treeview"
            role="menu"
            data-accordion="false"
          >
            <li className="nav-item">
              <Link to="/login" className="nav-link">
                <a href="/" className="nav-link">
                  <i className="nav-icon fas fa-sign-in-alt" style={{ fontSize: '1.4em' }} />
                  <p>Login</p>
                </a>
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/register" className="nav-link">
                <a href="/" className="nav-link">
                  <i className="nav-icon fa fa-user-plus" />
                  <p>Register</p>
                </a>
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </aside>
  );
}

export default Sidebar;

