import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import DataTable from "react-data-table-component";
import Api from "../../helpers/Api";
import ContentHeader from "../../components/ContentHeader";

const EditAttendance = () => {
  const [attendees, setAttendees] = useState([]);
  const [absentees, setAbsentees] = useState([]);
  const [eventTitle, setEventTitle] = useState("");
  const { eventId = 0 } = useParams();

  useEffect(() => {
    reloadData();
  }, [eventId]);

  const reloadData = () => {
    Api.getEvent(eventId)
      .then((res) => res.json())
      .then((event) => {
        const eventTitle = event.eventTitle;
        const registered = event.registrationsForEvent.filter(r => r.attendanceStatus);
        const absent = event.registrationsForEvent.filter(r => !r.attendanceStatus);
        setAttendees(registered);
        setAbsentees(absent);
        setEventTitle(eventTitle);
      })
      .catch((error) => console.error("Error fetching event data:", error));
  };

  const handleToggleAttendance = (personId, eventId) => {
    Api.changeAttendance(personId, eventId)
      .then(() => {
        reloadData(); // After API call, reload data to update the tables
      })
      .catch((error) => console.error("Error changing attendance status:", error));
  };

  const buttonStyle = {
    cursor: 'pointer',
    backgroundColor: '#007bff',
    color: 'white',
    padding: '5px 10px', // Reduced padding
    border: 'none',
    borderRadius: '5px',
    fontSize: '14px', // Reduced font size
    lineHeight: '1.5', // Ensuring proper line height for smaller text
    textAlign: 'center',
    display: 'inline-block', // Makes the button respect width and padding
    margin: '0 5px',
  };
  
  // ... rest of your component remains unchanged
  

  // Define columns for Attendees and Absentees tables
  const columns = [
    {
      name: "Name",
      selector: (registration) => registration.registeredPerson.name,
    },
    {
      cell: (registration) => (
        <button
          style={buttonStyle}
          onClick={() => handleToggleAttendance(registration.registeredPerson.personId, registration.registrationId)}
        >
          {registration.attendanceStatus ? "Mark as Absent" : "Mark as Attendee"}
        </button>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];

  const tableStyle = {
    table: {
      style: {
        borderCollapse: 'separate',
        borderSpacing: '0',
        border: '0.5px solid black', // Add black border around the table
      },
    },
    headRow: {
      style: {
        backgroundColor: '#f7f7f7',
        minHeight: '56px',
        borderBottomColor: '#dddddd',
        borderBottomWidth: '1px',
        borderBottomStyle: 'solid',
      },
    },
    headCells: {
      style: {
        fontSize: '22px',
        color: '#333',
        paddingLeft: '16px',
        paddingRight: '16px',
      },
    },
    rows: {
      style: {
        fontSize: '18px',
        color: '#333',
        borderBottomColor: '#dddddd',
        borderBottomWidth: '1px',
        borderBottomStyle: 'solid',
        padding : "5px"
      },
    },
  };
  

// ...

return (
  <>
    <ContentHeader
      title={`Edit Attendance for ${eventTitle}`}
      links={[
        { to: "/", label: "Home" },
        {
          to: "/searchCustomers",
          label: "My Created Events",
        },
        {
          label: "Edit Attendance",
          isActive: true,
        },
      ]}
    />
    <div style={{ margin: '20px' }}>
      <h3 style={{ marginBottom: '20px' }}>Registered Users</h3>
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <div style={{ flex: 1, marginRight: '10px' }}>
          <h4>Attendees</h4>
          <DataTable
            columns={columns}
            data={attendees}
            noHeader
            dense
            customStyles={tableStyle}
          />
        </div>
        <div style={{ flex: 1, marginLeft: '10px' }}>
          <h4>Absentees</h4>
          <DataTable
            columns={columns}
            data={absentees}
            noHeader
            dense
            customStyles={tableStyle}
          />
        </div>
      </div>
    </div>
  </>
);

};

export default EditAttendance;

