import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Api from "../helpers/Api";
import { useAuth } from "../context/AuthContext"; // Adjust the path accordingly
import ContentHeader from "../components/ContentHeader";

const Login = () => {
  const [credentials, setCredentials] = useState({
    username: "",
    password: "",
  });
  const navigate = useNavigate();
  const { login } = useAuth(); // Destructure the `login` function from the context

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCredentials({
      ...credentials,
      [name]: value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const response = await Api.authenticateUser(credentials);
    if (response.ok) {
      const data = await response.json();
      login(data.token); // Use the `login` function from context to update the auth state
      navigate("/"); // Redirect to home page or dashboard
    } else {
      // Handle login failure, such as displaying an error message
      window.alert("Login Credentials Failed");
    }
  };

  return (
    <>
      <ContentHeader
        title="Login"
        links={[
          {
            label: "Login",
            isActive: true,
          },
        ]}
      />
   
      <section className="content" key="content">
        <div className="card card-primary">
          <div className="card-header">
            <h3 className="card-title">Enter your credentials</h3>
          </div>
          <form onSubmit={handleSubmit}>
            <div className="card-body">
              <div className="form-group">
                <label htmlFor="inputUsername">User Name</label>
                <input
                  type="text"
                  id="inputUsername"
                  name="username" // Make sure the `name` matches the key in credentials state
                  required
                  className="form-control"
                  value={credentials.username}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group">
                <label htmlFor="inputPassword">Password</label>
                <input
                  type="password"
                  id="inputPassword"
                  name="password" // Make sure the `name` matches the key in credentials state
                  required
                  className="form-control"
                  value={credentials.password}
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className="card-footer">
              <button className="btn btn-primary float-right" type="submit">
                Submit
              </button>
            </div>
          </form>
        </div>
      </section>
    </>
  );
};

export default Login;


