import React, { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import DatePicker from "react-datepicker";
import Api from "../../helpers/Api";
import moment from "moment";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Image from "react-bootstrap/Image";
import Row from "react-bootstrap/Row";

import "react-datepicker/dist/react-datepicker.css";
import ContentHeader from "../../components/ContentHeader";

function ProfilePage(props) {
  //this is the page that is used for creating new person (REGISTERING NEW ACCOUNT)and editing person details (profile page)
  //THESE ARE THE ATTRIBUTES OF PERSON userName, password, name, contactNumber, email, profilePicture
  //const { id = 0 } = useParams();
  const [name, setName] = useState("");
  const [contactNumber, setContactNumber] = useState("");
  const [email, setEmail] = useState("");
  const [profilePicture, setProfilePicture] = useState("");

  const navigate = useNavigate();

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file && file.type.match("image.*")) {
      const reader = new FileReader();
      reader.onload = (readEvent) => {
        setProfilePicture(readEvent.target.result); // Base64 string for the image
        console.log("profilepic is" + readEvent.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  //load for the edit case - edit profilePicture
  useEffect(() => {
    Api.getPerson()
      .then((res) => res.json())
      .then((person) => {
        console.log("Current Person ID: ", person);
        console.log("Person data received:", person);
        const { name, contactNumber, email, profilePicture } = person;
        setName(name);
        setContactNumber(contactNumber);
        setEmail(email);
        setProfilePicture(profilePicture);
      });
  }, []);

  const validateData = () => {
    return (
      name.trim().length > 0 &&
      contactNumber.trim().length == 8 &&
      /^\d{8}$/.test(contactNumber) &&
      email.trim().length > 0 &&
      email.endsWith("@gmail.com") &&
      profilePicture.trim().length > 0
    );
  };

  const handleSubmit = (e) => {
    //prevent the normal form submit
    e.preventDefault();
    console.log("hey chica");

    if (validateData()) {
      console.log("erm");
      Api.updatePerson({
        name,
        contactNumber,
        email,
        profilePicture,
      }).then((data) => {
        window.alert("Saved Profile Details");
        navigate("/profilePage");
      });
    } else {
      if (contactNumber.trim().length !== 8 || !/^\d{8}$/.test(contactNumber)) {
        window.alert(
          "Contact Number must be exactly 8 digits and only contain numbers."
        );
      } else if (!email.endsWith("@gmail.com")) {
        window.alert("Please enter valid username that ends with @gmail.com");
      } else if (profilePicture.trim().length == 0) {
        window.alert("Please select profile picture");
      }
    }
  };

  return (
    <>
      <ContentHeader
        title="Profile Page"
        links={[
          {
            to: "/",
            label: "Home",
          },
          {
            label: "Edit Profile Page",
            isActive: true,
          },
        ]}
      />
      <section className="content" key="content">
        <div className="card card-primary">
          <div className="card-header">
            <h3 className="card-title">{"Edit Profile Page"}</h3>
          </div>
          <form onSubmit={handleSubmit}>
            <div className="card-body">
              <Container>
                <Row className="justify-content-center">
                  {/* Image container */}
                  <Col md={6} className="text-center">
                    {profilePicture && (
                      <div
                        style={{
                          position: "relative",
                          display: "inline-block",
                        }}
                      >
                        <Image
                          src={profilePicture}
                          alt="Profile Preview"
                          style={{ width: "200px", height: "200px" }}
                          thumbnail
                        />
                      </div>
                    )}
                  </Col>
                </Row>
                <Row className="justify-content-end">
                  {/* Upload button */}
                  <Col
                    md={6}
                    className="text-right"
                    style={{ marginTop: "-40px" }}
                  >
                    <label
                      htmlFor="inputProfilePicture"
                      className="btn btn-primary"
                    >
                      Upload Image
                    </label>
                    <input
                      type="file"
                      id="inputProfilePicture"
                      className="form-control-file"
                      onChange={handleImageChange}
                      style={{ display: "none" }}
                    />
                  </Col>
                </Row>
              </Container>

              <div className="form-group">
                {" "}
                <label htmlFor="inputName">Name</label>
                <input
                  type="text"
                  id="inputName"
                  required
                  className="form-control"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputContactNumber">Contact Number</label>
                <input
                  type="text"
                  id="inputContactNumber"
                  required
                  className="form-control"
                  value={contactNumber}
                  onChange={(e) => setContactNumber(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputEmail">Email</label>
                <input
                  type="text"
                  id="inputEmail"
                  required
                  className="form-control"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
            </div>
            <div className="card-footer">
              <button className="btn btn-primary float-right" type="submit">
                Submit
              </button>
            </div>
          </form>
        </div>
      </section>
    </>
  );
}

export default ProfilePage;

