import React, { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import DatePicker from "react-datepicker";
import Api from "../../helpers/Api";
import moment from "moment";
import Image from "react-bootstrap/Image";

import "react-datepicker/dist/react-datepicker.css";
import ContentHeader from "../../components/ContentHeader";

function Register(props) {
  //this is the page that is used for creating new person (REGISTERING NEW ACCOUNT)and editing person details (profile page)
  //THESE ARE THE ATTRIBUTES OF PERSON userName, password, name, contactNumber, email, profilePicture
  //const { id = 0 } = useParams();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [name, setName] = useState("");
  const [contactNumber, setContactNumber] = useState("");
  const [email, setEmail] = useState("");
  const [profilePicture, setProfilePicture] = useState("");

  const navigate = useNavigate();

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file && file.type.match("image.*")) {
      const reader = new FileReader();
      reader.onload = (readEvent) => {
        setProfilePicture(readEvent.target.result); // Base64 string for the image
        console.log("profilepic is" + readEvent.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const validateData = () => {
    return (
      username.trim().length > 0 &&
      password.trim().length > 0 &&
      name.trim().length > 0 &&
      contactNumber.trim().length == 8 &&
      /^\d{8}$/.test(contactNumber) &&
      email.trim().length > 0 &&
      email.endsWith("@gmail.com") &&
      profilePicture.trim().length > 0
    );
  };

  const handleSubmit = (e) => {
    //prevent the normal form submit
    e.preventDefault();
    console.log("hey chica");

    if (validateData()) {
      console.log("erm");
      Api.createPerson({
        username,
        password,
        name,
        contactNumber,
        email,
        profilePicture,
      }).then((data) => {
        navigate("/login");
      });
    } else {
      if (contactNumber.trim().length !== 8 || !/^\d{8}$/.test(contactNumber)) {
        window.alert(
          "Contact Number must be exactly 8 digits and only contain numbers."
        );
      } else if (!email.endsWith("@gmail.com")) {
        window.alert("Please enter valid username that ends with @gmail.com");
      } else if (profilePicture.trim().length == 0) {
        window.alert("Please select profile picture");
      }
    }
  };

  return (
    <>
      <ContentHeader
        title="Register"
        links={[
          {
            to: "/",
            label: "Login",
          },
          {
            label: "Create Account",
            isActive: true,
          },
        ]}
      />
      <section className="content" key="content">
        <div className="card card-primary">
          <div className="card-header">
            <h3 className="card-title">{"Create Account"}</h3>
          </div>
          <form onSubmit={handleSubmit}>
            <div className="card-body">
              <div className="form-group">
                {" "}
                <label htmlFor="inputUsername">User Name</label>
                <input
                  type="text"
                  id="inputUsername"
                  required
                  className="form-control"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputPassword">Password</label>
                <input
                  type="password"
                  id="inputPassword"
                  required
                  className="form-control"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputName">Name</label>
                <input
                  type="text"
                  id="inputName"
                  required
                  className="form-control"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputContactNumber">Contact Number</label>
                <input
                  type="text"
                  id="inputContactNumber"
                  required
                  className="form-control"
                  value={contactNumber}
                  onChange={(e) => setContactNumber(e.target.value)}
                />
              </div>
              <div className="form-group">
                {" "}
                <label htmlFor="inputEmail">Email</label>
                <input
                  type="text"
                  id="inputEmail"
                  required
                  className="form-control"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="form-group">
                <label htmlFor="inputProfilePicture">Profile Picture</label>
                {profilePicture && (
                  <div style={{ margin: "1em 0" }}>
                    <Image
                      src={profilePicture}
                      alt="Profile Preview"
                      style={{ width: "200px", height: "200px" }}
                      thumbnail
                    />
                  </div>
                )}
                <input
                  type="file"
                  id="inputProfilePicture"
                  className="form-control"
                  onChange={handleImageChange}
                />
              </div>
            </div>
            <div className="card-footer">
              <button className="btn btn-primary float-right" type="submit">
                Submit
              </button>
            </div>
          </form>
        </div>
      </section>
    </>
  );
}

export default Register;
