import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Modal from "react-responsive-modal";
import moment from "moment";
import DataTable from "react-data-table-component";

import Api from "../../helpers/Api";
import "react-responsive-modal/styles.css"; //Import css
import ContentHeader from "../../components/ContentHeader";

function SearchRegistrations(props) {
  //console.log(localStorage.getItem('token'));

  const [data, setData] = useState([]);
  //const [selectedCustomerId, setSelectedCustomerId] = useState(-1);

  useEffect(() => {
    reloadData();
  }, []);

  const reloadData = () => {
    Api.getRegistrations()
      .then((res) => res.json())
      .then((registrations) => {
        console.log("Registrations fetched successfully");
        for (const registration of registrations) {
          const {
            registrationId,
            registrationDate,
            attendanceStatus,
            registeredPerson,
            event,
          } = registration;
          let { date } = event;
          registration.registrationDate = registrationDate.substring(
            0,
            registrationDate.length - 5
          );
          registration.event.date = registration.event.date.replace(
            "[UTC]",
            ""
          );
        }
        setData(registrations);
      });
  };
  // const handleDeleteRegistration = (registrationId) => {
  //   console.log("Attempting to delete registration:", registrationId);
  //   if (window.confirm(`Do you want to delete registration?`)) {
  //     Api.deleteRegistration(registrationId).then(() => {
  //       reloadData();
  //     });
  //   }
  // };

  ///
  const handleDeleteRegistration = async (registrationId) => {
    let isRegistrationAvailForUnreg = false;

    await Api.checkUnregistrationAbility(registrationId)
      .then((response) => response.json()) // Assuming the server responds with JSON
      .then((data) => {
        console.log("UNNNNNregistration ability:", data);
        // Assuming 'data' contains the boolean value directly or an object from which the boolean can be extracted
        // For example, if the server returns { registered: true/false }
        isRegistrationAvailForUnreg = data.isRegistrationAvailForUnreg;
        console.log(
          "Can I still unregister for the event?:",
          isRegistrationAvailForUnreg
        );
      })
      .catch((error) => {
        console.error("Error checking isRegistrationAvailForUnreg", error);
      });

    if (isRegistrationAvailForUnreg == false) {
      window.alert("You can ONLY unregister before the start of event.");
    } else {
      console.log("Attempting to delete registration:", registrationId);
      if (window.confirm(`Do you want to delete registration?`)) {
        Api.deleteRegistration(registrationId).then(() => {
          reloadData();
        });
      }
    }
  };

  ////

  const columns = [
    {
      name: "Event Title",
      sortable: true,
      selector: (registration) => registration.event.eventTitle,
    },
    {
      name: "Event Date",
      sortable: true,
      selector: (registration) =>
        moment(registration.event.date).format("DD/MM/YYYY"),
    },
    {
      name: "Attendance Status",
      sortable: true,
      selector: (registration) =>
        registration.attendanceStatus ? "yes" : "no",
    },
    {
      name: "Registered Date",
      sortable: true,
      selector: (registration) =>
        moment(registration.registrationDate).format("DD/MM/YYYY"),
    },
    {
      name: "",
      button: true,
      width: "150px",
      cell: ({ registrationId }) => {
        return (
          <>
            {/* <Link to={`/manageCustomer/${eventId}`}>
              <button data-type="edit" className="btn btn-default">
                <i className="fa fa-fw fa-edit" />
              </button>
            </Link> */}
            <button
              data-type="delete"
              className="btn btn-default"
              onClick={() => handleDeleteRegistration(registrationId)}
            >
              <i className="fa fa-fw fa-trash" />
            </button>
          </>
        );
      },
    },
  ];

  return (
    <>
      <ContentHeader
        title="View my Registrations"
        links={[
          {
            to: "/",
            label: "Home",
          },
          {
            label: "List Registrations",
            isActive: true,
          },
        ]}
      />
      <section className="content" key="content">
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-header">
                <h3 className="card-title">Search Registrations</h3>
              </div>
              <div className="card-body">
                <DataTable
                  columns={columns}
                  data={data}
                  highlightOnHover
                  pagination
                  paginationPerPage={5}
                  paginationRowsPerPageOptions={[5, 10, 50]}
                  sortable
                />
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default SearchRegistrations;
