import React, { useState, useEffect, useCallback } from "react";
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import DatePicker from "react-datepicker";
import moment from "moment";
import DataTable from "react-data-table-component";
import { Link } from "react-router-dom";
import "react-datepicker/dist/react-datepicker.css";

import Api from "../../helpers/Api";
import ContentHeader from "../../components/ContentHeader";


function SearchCustomers() {
  const [data, setData] = useState([]);
  const [filterText, setFilterText] = useState("");
  const [filterType, setFilterType] = useState("eventTitle");
  const [selectedDate, setSelectedDate] = useState(null);

  useEffect(() => {
reloadData();
  }, []);

    const reloadData = useCallback(() => {
    Api.getAllCreatedEvents()
      .then(res => res.json())
      .then(events => {
        events = events.map(event => ({
          ...event,
          date: event.date.substring(0, event.date.length - 5),
          deadline: event.deadline.substring(0, event.deadline.length - 5),
        }));
        setData(events);
      });
  }, []);

  const handleDeleteEvent = (eventId) => {
    console.log("Attempting to delete event:", eventId);
    if (window.confirm(`Do you want to delete event?`)) {
      Api.deleteEvent(eventId).then(() => {
        reloadData();
      });
    }
  };

  const handleFilterTextChange = (e) => {
    setFilterText(e.target.value);
  };

  const handleFilterTypeChange = (e) => {
    setFilterType(e.target.value);
    setFilterText("");
    setSelectedDate(null);
  };

  const getFilteredEvents = () => {
    return data.filter((event) => {
      if (filterType === "date" && selectedDate) {
        return moment(event.date).isSame(selectedDate, "day");
      }
      return event[filterType]
        ?.toLowerCase()
        .includes(filterText.toLowerCase());
    });
  };

  const columns = [
    {
      name: "Event Title",
      sortable: true,
      selector: (event) => event.eventTitle,
    },
    {
      name: "Location",
      sortable: true,
      selector: (event) => event.location,
    },
    {
      name: "Description",
      sortable: true,
      selector: (event) => event.description,
    },
    {
      name: "Date of Event",
      sortable: true,
      selector: (event) => moment(event.date).format("DD/MM/YYYY"),
    },
    {
      name: "Deadline",
      sortable: true,
      selector: (event) => moment(event.deadline).format("DD/MM/YYYY"),
    },
    {
      name: "",
      button: true,
      width: "150px",
      cell: ({ eventId }) => {
        return (
          <>
            <Link to={`/editAttendance/${eventId}`}>
              <button data-type="edit" className="btn btn-default">
              <i className="fa fa-fw fa-search" />
              </button>
            </Link>
            <Link to={`/manageCustomer/${eventId}`}>
              <button data-type="edit" className="btn btn-default">
                <i className="fa fa-fw fa-edit" />
              </button>
            </Link>
            <button
              data-type="delete"
              className="btn btn-default"
              onClick={() => handleDeleteEvent(eventId)}
            >
              <i className="fa fa-fw fa-trash" />
            </button>
          </>
        );
      },
    },
  ];



  return (
    <>
      <ContentHeader
        title="My Created Events"
        links={[
          { to: "/", label: "Home" },
          { label: "My Created Events", isActive: true },
        ]}
      />
      <Container fluid>
        <Row className="mb-3">
          <Col md={3}> {/* Adjust this to align with the start of the DataTable */}
            <Form.Control
              as="select"
              value={filterType}
              onChange={handleFilterTypeChange}
            >
              <option value="eventTitle">Event Title</option>
              <option value="description">Description</option>
              <option value="location">Location</option>
              <option value="date">Date</option>
            </Form.Control>
          </Col>
          <Col md={3}> {/* Adjust this as well if needed */}
            {filterType === "date" ? (
              <DatePicker
                selected={selectedDate}
                onChange={(date) => setSelectedDate(date)}
                className="form-control"
                dateFormat="dd/MM/yyyy"
                placeholderText="Select a date"
              />
            ) : (
              <Form.Control
                type="text"
                value={filterText}
                onChange={handleFilterTextChange}
                placeholder={`Search by ${filterType}`}
              />
            )}
          </Col>
        </Row>
      </Container>

      <section className="content" key="content">
        <div className="row">
          <div className="col-12">
            <div className="card">
              {/* ... card header */}
              <div className="card-body">
                <DataTable
                  columns={columns}
                  data={getFilteredEvents()}
                  highlightOnHover
                  pagination
                  paginationPerPage={5}
                  paginationRowsPerPageOptions={[5, 10, 50]}
                  sortable
                />
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}


export default SearchCustomers;



