import React, { useState, useEffect } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import { AuthProvider } from "./context/AuthContext";

import Navbar from "./components/Navbar";
import Sidebar from "./components/Sidebar";
import Footer from "./components/Footer";
import Index from "./components/Index";

import ManageCustomer from "./containers/ManageCustomer";
import SearchCustomers from "./containers/SearchCustomers";
import Login from "./pages/Login"; // Import your Login component
import RegisterForEvent from "./containers/RegisterForEvent";
import SearchRegistrations from "./containers/SearchRegistrations";
import EditAttendance from "./containers/EditAttendance";
import ProfilePage from "./containers/ProfilePage/index";
import Register from "./containers/Register";
import SplashScreen from "./pages/SplashScreen";

// Helper function to check if the user is authenticated
const isAuthenticated = () => {
  // Check if the token exists in local storage (or your chosen method of storage)
  const token = localStorage.getItem("token");
  return !!token;
};

// A wrapper for <Route> that redirects to the login screen if you're not yet authenticated.
const PrivateRoute = ({ children }) => {
  return isAuthenticated() ? children : <Navigate to="/login" />;
};

function App() {


  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(false);
    }, 2000); // Change this to however long you want the splash screen to show

    return () => clearTimeout(timer);
  }, []);

  if (loading) {
    return <SplashScreen />;
  }















  return (
    <AuthProvider>
      <div className="wrapper">
        {/* Render Navbar and Sidebar only if authenticated */}

        <Navbar />
        <Sidebar />

        {/* Content wrapper could be used to wrap the Routes as well */}
        <div className="content-wrapper">
          <Routes>
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            <Route
              path="/manageCustomer/:id"
              element={
                <PrivateRoute>
                  <ManageCustomer />
                </PrivateRoute>
              }
            />
            <Route
              path="/manageCustomer"
              element={
                <PrivateRoute>
                  <ManageCustomer />
                </PrivateRoute>
              }
            />
            <Route
              path="/searchCustomers"
              element={
                <PrivateRoute>
                  <SearchCustomers />
                </PrivateRoute>
              }
            />
            <Route
              path="/RegisterForEvent"
              element={
                <PrivateRoute>
                  <RegisterForEvent />
                </PrivateRoute>
              }
            />
            <Route
              path="/SearchRegistrations"
              element={
                <PrivateRoute>
                  <SearchRegistrations />
                </PrivateRoute>
              }
            />
            <Route
              path="/editAttendance/:eventId"
              element={
                <PrivateRoute>
                  <EditAttendance />
                </PrivateRoute>
              }
            />
            <Route
              path="/profilePage"
              element={
                <PrivateRoute>
                  <ProfilePage />
                </PrivateRoute>
              }
            />
            <Route
              path="/"
              element={
                <PrivateRoute>
                  <Index />
                </PrivateRoute>
              }
            />
            {/* Other routes */}
          </Routes>
        </div>

        {/* Footer is always rendered */}
        <Footer />
      </div>
    </AuthProvider>
  );
}

export default App;
