import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { HashRouter } from "react-router-dom";

//Font Awesome
import "admin-lte/plugins/fontawesome-free/css/all.min.css";
//Theme style (comes with Bootstrap css)
import "admin-lte/dist/css/adminlte.min.css";
//AdminLTE script
//we have to use the require() instead of ES6 import here
//this is because import statements should be before any codes but
//since AdminLTE requires jQuery to work properly, we cant use the
//import syntax
require("admin-lte/dist/js/adminlte.min");

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <HashRouter>
      <App />
    </HashRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
